export const invert = (theme) => {
  if (theme === '') return '-dark';
  return '';
}

// Todas as cores são convertidas em preto, exceto a cor -dark que é convertida para branco, o invert, inverte entre dark e light;

export const convertColor = (color, isTheme = false) => {
  switch (color) {
    case '':
      return isTheme ? '#fff' : '#242424';
    case '-dark':
      return isTheme ? '#242424' : '#fff';
    case '-blue':
      return '#9599e2';
    case '-yellow':
      return '#fbab7e';
    case '-green':
      return '#36cda2';
    case '-purple':
      return '#8a51ec';
    case '-pink':
      return '#f7a49f';
    case '-red':
      return '#ea5455';
    case '-orange':
      return '#ff7a5f';
    case '-coffee':
      return '#83624a';
    case '-luxe':
      return '#CDC2AE';
    default:
      return '#fff';
  }
}
